<template>
	<view class="content">
			<view>
				<u-picker @cancel="xxx" mode="region" v-model="show" :area-code='["13", "1303", "130304"]'></u-picker>
				<u-button @click="show = true">打开</u-button>
			</view>
		
		<view class="">
			<u-form :model="form" ref="uForm">
				<u-form-item label="姓名" prop="name"><u-input v-model="form.name" /></u-form-item>
				<u-form-item label="简介" prop="intro"><u-input v-model="form.intro" /></u-form-item>
			</u-form>
			<u-button @click="submit">提交</u-button>
		</view>
		<view class="img">
			<image src="../../static/logo.png" mode="widthFix"></image>
		</view>
		<text class="text">自主创新</text>
		<u-button>默认按钮</u-button>
		<u-button type="primary">主要按钮</u-button>
		<u-button type="success" shape="circle" plain ripple>成功按钮</u-button>
		<u-button type="info">信息按钮</u-button>
		<u-button type="warning">警告按钮</u-button>
		<u-button type="error">危险按钮</u-button>
		<u-button size="default">江湖</u-button>
		<u-button size="medium">
			<u-icon name="plus" color="#2979ff" size="28"></u-icon>
			夜雨
		</u-button>
		<u-button size="mini">十年灯</u-button>
		<u-icon name="photo"></u-icon>
		<u-cell-group>
			<u-cell-item label="12123" icon="photo" title="个人设置"></u-cell-item>
			<u-badge type="error" count="7"></u-badge>
			<u-cell-item icon="integral-fill" title="会员等级" value="新版本"></u-cell-item>
		</u-cell-group>
		<u-cell-group>
			<u-cell-item title="夕阳无限好" arrow-direction="down">
				<u-icon slot="icon" size="32" name="search"></u-icon>
				<!-- <u-badge count="99" :absolute="false" slot="right-icon"></u-badge> -->
				<u-switch slot="right-icon" v-model="checked"></u-switch>
			</u-cell-item>
			<u-cell-item icon="setting-fill" title="只是近黄昏"></u-cell-item>
		</u-cell-group>
		<u-tag text="雪月夜" type="error" />
		<u-tag text="一丘之貉" mode="dark" />
		<u-tag text="沆瀣一气" mode="light" />
		<u-tag text="狼狈为奸" mode="plain" />
		<u-tag text="主谓宾" shape="circle" />
		<u-tag text="定状补" shape="circleLeft" />
	</view>
</template>

<script>
export default {
	data() {
		return {
			form: {
				name: '',
				intro: ''
			},
			rules: {
				name: [
					{
						required: true,
						message: '请输入姓名',
						// 可以单个或者同时写两个触发验证方式
						trigger: ['change', 'blur']
					}
				],
				intro: [
					{
						min: 5,
						message: '简介不能少于5个字',
						trigger: 'change'
					}
				]
			},
			show: false
		};
	},
	onLoad() {
		this.$refs.uForm.setRules(this.rules);
	},
	methods: {
		submit() {
			this.$refs.uForm.validate(valid => {
				if (valid) {
					console.log('验证通过');
				} else {
					console.log('验证失败');
				}
			});
		},
		xxx(e){
			console.log(e);
		}
	}
};
</script>

<style lang="scss">
.content {
	.text {
		color: $themeColor;
	}
	.img{
		width: 100rpx;
	}
}
.wrap {
	padding: 24rpx;
}

.u-row {
	margin: 40rpx 0;
}

.demo-layout {
	height: 80rpx;
	border-radius: 8rpx;
}

.bg-purple {
	background: #d3dce6;
}

.bg-purple-light {
	background: #e5e9f2;
}

.bg-purple-dark {
	background: #99a9bf;
}
</style>
